export const ID_ETAPA = Object.freeze({
  CALI: '01',
  PREL: '02',
  PREP: '03',
  INTE: '04',
  JUZG: '05',
  EJEC: '06',
});
export enum TipoOpcionCasoFiscal {
  Ninguna = 0,
  Calificacion = 1,
  Preliminar = 2,
  Preparatoria = 3,
  Intermedia = 4,
  Juzgamiento = 5,
  Ejecucion = 6,
  Impugnacion = 7,
  ProcesosEspeciales = 8,
  Concluidos = 9,
}
export const ETAPA = [
  {
    id: ID_ETAPA.CALI,
    nombre: 'Calificación',
    path: 'calificacion',
    tipoOpcion: TipoOpcionCasoFiscal.Calificacion,
  },
  {
    id: ID_ETAPA.PREL,
    nombre: 'Preliminar',
    path: 'preliminar',
    tipoOpcion: TipoOpcionCasoFiscal.Preliminar,
  },
  {
    id: ID_ETAPA.PREP,
    nombre: 'Preparatoria',
    path: 'preparatoria',
    tipoOpcion: TipoOpcionCasoFiscal.Preparatoria,
  },
  {
    id: ID_ETAPA.INTE,
    nombre: 'Intermedia',
    path: 'intermedia',
    tipoOpcion: TipoOpcionCasoFiscal.Intermedia,
  },
  {
    id: ID_ETAPA.JUZG,
    nombre: 'Juzgamiento',
    path: 'juzgamiento',
    tipoOpcion: TipoOpcionCasoFiscal.Juzgamiento,
  },
  {
    id: ID_ETAPA.EJEC,
    nombre: 'Ejecución',
    path: 'ejecucion',
    tipoOpcion: TipoOpcionCasoFiscal.Ejecucion,
  },
];

export function etapaInfo(id: string): any {
  return ETAPA.find((etapa: any) => etapa.id === id);
}
