import {Injectable} from '@angular/core';
import {DomSanitizer} from "@angular/platform-browser";
import {Delito} from "../interfaces/comunes/casosFiscales-interface";

@Injectable({
  providedIn: 'root'
})
export class StringUtil {

  constructor(
    private sanitizer: DomSanitizer
  ) {}

  public delitosArray!: Delito[];

  // TODO: Ref cfng-core-lib - formatearCodigoCaso
  //  - Se debera llamar a la librería de estilo(cfng-core-cdn), para referenciar el color del texto sobre el código de caso.
  public formatearCodigoCaso(codigoCaso: string) {
    codigoCaso = `${codigoCaso}-0`
    const partes = codigoCaso.split('-');
    if (partes.length > 3) {
      return this.sanitizer.bypassSecurityTrustHtml(`${partes[0]}-<span style="color:orange" >${partes[1]}-${partes[2]}</span>-${partes[3]}`)
    }
    return codigoCaso;
  }

  // TODO: Ref cfng-core-lib - obtenerNumeroCaso
  public obtenerNumeroCaso(numeroCaso: string): string {
    const caso = numeroCaso.split('-')
    return `<div class="cfe-caso">${caso[0]}-<span>${caso[1]}-${caso[2]}</span>-${caso[3]}</div>`
  }

  // TODO: Ref cfng-core-lib - obtenerClaseDeOrigen
  public obtenerPlazo(plazo: string): string {
    let semaforo = 'dentro-del-plazo';
    if (plazo == "2") {
      semaforo = 'plazo-por-vencer';
    } else if (plazo == "3") {
      semaforo = 'plazo-vencido';
    }
    return semaforo
  }

  public obtenerPlazoItem(plazo: string): string {
    let semaforo = 'dentro-del-plazo';
    if (plazo == "2") {
      semaforo = 'plazo-por-vencer';
    } else if (plazo == "3") {
      semaforo = 'plazo-vencido';
    }
    return `<span class="plazo-item ${semaforo}"></span>`
  }

  public formatearNombre(nombre: string): string {
    if (nombre !== undefined && nombre !== null) {
      return nombre
        .toLowerCase()
        .replace(/(?:^|\s)\S/g, (char) => char.toUpperCase());
    } else {
      return '-';
    }
  }

  public mostrarDelitosFromArray(delitoArray: Delito[]): string {
    if (delitoArray && delitoArray.length > 0) {
      return delitoArray
        .map((item) => item.nombre!.toLowerCase())
        .join(' / ');
    } else {
      return '-';
    }
  }

  public getColor(indSemaforo: number) {
    switch (indSemaforo) {
      case 1:
        return 'greenbar';
      case 2:
        return 'yellowbar';
      case 3:
        return 'redbar';
      default:
        return 'yellowbar';
    }
  }

  public capitalizedFirstWord(texto: any = '' ) {
    if (typeof texto !== 'string' || texto.length === 0) {
      return texto;
    }
    return texto.charAt(0).toUpperCase() + texto.slice(1).toLowerCase();
  }

}

